<template>
  <div id="new-restaurant">
    <h3>New Restaurant</h3>
    <div>
      <form @submit.prevent="saveRestaurant">
        <label>Name: </label>
        <input type="text" v-model="name" required>


        <select v-model="location">
          <option value="Lisboa">Lisboa</option>
          <option value="Barreiro">Barreiro</option>
        </select>

        <select class="radio" v-model="price">
          <option type="radio" name="cost" value="$"> $</option>
          <option type="radio" name="cost" value="$$"> $$</option>
          <option type="radio" name="cost" value="$$$"> $$$</option>
        </select>

        <label>Zomato Link: </label>
        <input type="text" v-model="zomato_link">

        <label>Obs: </label>
        <input type="text" v-model="obs">

        <button type="submit" class="submitBtn">Submit</button>
        <router-link to="/">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-restaurant',
  data (){
    return{
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null
    }
  },
  methods: {
    saveRestaurant(){
      db.collection('restaurants').add({
        name: this.name,
        location: this.location,
        price: this.price,
        zomato_link: this.zomato_link,
        obs: this.obs
      })
      .then(docRef => this.$router.push('/'))
      .catch(error => console.log(err))
    }
  }
}
</script>

<style scoped>

</style>



